.coaches {
  position: relative;
  margin: 0 auto;
  margin-bottom: 120px;
  padding: 0 103px;
  max-width: $vp-1200;

  @media (min-width: $vp-1366) {
    padding: 0 20px;
  }

  @include vp-1199 {
    padding: 0 101px;
  }

  @include vp-767 {
    padding: 0 15px;
  }
}

.coaches h2 {
  margin: 0;
  margin-bottom: 50px;
  font-family: $ff-bebas-neue;
  font-size: 49px;
  line-height: 49px;
  letter-spacing: 0.03em;

  @include vp-1199 {
    margin-bottom: 41px;
    font-size: 42px;
    line-height: 42px;
    text-align: center;
  }

  @include vp-767 {
    margin-bottom: 43px;
    font-size: 32px;
    line-height: 32px;
  }
}

.coaches__item {
  height: 294px;

  @include vp-1199 {
    min-height: 294px;
    height: auto;
    aspect-ratio: 0.95 / 1;
  }

  @include vp-767 {
    min-height: 274px;
  }
}

.coaches__swiper {
  box-sizing: content-box;
  position: relative;
  left: 50%;
  transform: translate(-50%);
  padding: 0 60px;
  width: 100%;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 0;
    width: 60px;
    height: 100%;
    background-color: $color-default-white;
    z-index: 2;

    @include vp-767 {
      width: 32px;
    }
  }

  &::before {
    left: 0;
  }

  &::after {
    right: 0;
  }

  @include vp-767 {
    padding: 0 32px;
    left: 0;
    transform: none;
    width: auto;
  }
}

.coaches__button {
  &--prev {
    left: 0;

    @include vp-767 {
      left: -8px;
    }
  }

  &--next {
    right: 0;

    @include vp-767 {
      right: -8px;
    }
  }
}

.coaches__button::after {
  font-size: 33px;
  color: $color-dark-gray;

  @include vp-767 {
    font-size: 19px;
  }
}
